import React from "react";
import PropTypes from "prop-types";

export default class OraStockLevelStatusComponent extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        if(this.props.shouldStockLevelBeVisible) {
            return(
                <div>
                    <p>{"DostÄ™pnych sztuk: " + this.props.stockLevel}</p>
                    <p>{"Stan na " + this.props.stockLevelDate}</p>
                </div>);
        } else {
            return null;
        }
    }

    static isAvailableInStock(stockLevelStatus) {
        return !stockLevelStatus || stockLevelStatus.code != OraStockLevelStatusComponent.OUT_OF_STOCK;
    }

    static getStockLevelOrder(stockLevel, shouldStockLevelBeSorted) {
        if(!shouldStockLevelBeSorted || stockLevel == null) {
            return  0;
        } else {
            return stockLevel;
        }
    }

    static getStockCssClass(stockLevelStatus) {
        return OraStockLevelStatusComponent.isAvailableInStock(stockLevelStatus) ? "" : "g-redf-c";
    }
}

OraStockLevelStatusComponent.OUT_OF_STOCK = "outOfStock";
OraStockLevelStatusComponent.LOW_STOCK = "lowStock";
OraStockLevelStatusComponent.IN_STOCK = "inStock";


OraStockLevelStatusComponent.propTypes = {
    stockLevelStatus: PropTypes.shape({
        code: PropTypes.string,
        type: PropTypes.string,
    }),
    stockLevel: PropTypes.number,
    stockLevelDate: PropTypes.string,
    shouldStockLevelBeVisible: PropTypes.bool,
};

OraStockLevelStatusComponent.defaultProps = {
    stockLevelStatus: null,
    stockLevel: null,
    stockLevelDate: null,
    shouldStockLevelBeVisible: false,
};